import '../../../node_modules/@lit/reactive-element/reactive-element.js';
import '../../../node_modules/lit/node_modules/lit-html/lit-html.js';
import { html as p } from '../../../node_modules/lit-element/node_modules/lit-html/lit-html.js';
import { LitElement as s } from '../../../node_modules/lit-element/lit-element.js';
import { ifDefined as l } from '../../../node_modules/lit/node_modules/lit-html/directives/if-defined.js';
import textStyles from '../../styles/text/text.js';
import styles from './ButtonStyles.js';

class Button extends s {
  static get properties() {
    return {
      type: { type: String },
      label: { type: String },
      href: { type: String },
      icon: { type: String },
      trailingIcon: { attribute: 'trailing-icon', type: Boolean }, // Should the icon be placed after the text?
    };
  }

  static get styles() {
    return [textStyles, styles];
  }

  render() {
    const icon = p`<ion-icon part='icon' name="${this.icon}"></ion-icon>`;
    return p`
      <button part='container' href=${l(this.href)}>
        ${this.icon && !this.trailingIcon
          ? icon
          : null}
        <slot class="st-text"> ${this.label} </slot>
        ${this.icon && this.trailingIcon
          ? icon
          : null}
      </button>
    `;
  }
}

customElements.define('st-button', Button);

export default Button;
